.zoomBOut {
  width: 600px; 
  height: 338px;
  overflow: hidden;
  position: relative;
}
.zoomBIn {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  transition-duration: 1s;
  transition-timing-function: ease;
}
.zoomBIn:hover{
  transform: scale(1.2);
}